.app_layout {
  display: grid;
  grid-template-columns: 300px 1fr;
  height: 100%;
  max-height: 100vh;
  margin-left: 75px;
  .chat_container {
    position: relative;
    background-image: url("../../assets/images/chat-bg.png");
    background-color: #e2ebe5;
    max-width: 100%;
    overflow-y: auto;
  }
}
